import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],

})

export class AppComponent {
  title = 'app';
  public scrollstatus = window.scrollX;
  public viewportHeight = innerHeight;

  constructor(public router: Router) {
    //scroll to fragment
    router.events.subscribe(s => {
      if (s instanceof NavigationEnd) {
        const tree = router.parseUrl(router.url);
        if (tree.fragment) {
          const element = document.querySelector("#" + tree.fragment);
          if (element) { element.scrollIntoView(true); }
        }
      }
    });
  }

  ngOnInit() {
    window.addEventListener('scroll', this.scroll, true);
    //console.log('viewportHeight :' + this.viewportHeight);
  }

  scroll = (): void => {
    // console.log('scrollstatus : ' + window.scrollY);
    // if (window.scrollY > this.viewportHeight)
    // {
    //   console.log('CLASS is set');
    // }
    // else
    // {
    //   console.log('NO CLASS');
    // }
  }

  getScrollStatus()
  {
    return +window.scrollY;
  }
  
  scrollToTop() {
    window.scrollTo(0, 0);
  }
}



